<template>
  <div class="mdl-cell--4-col-phone">
    <div
      @click="toggleDialog"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button custom-button--right custom-button--primary"
    >
      Add Bank
    </div>
    <table class="mdl-data-table mdl-js-data-table">
      <thead>
        <tr>
          <th class="mdl-data-table__cell--non-numeric"> Bank </th>
          <th class="mdl-data-table__cell--non-numeric"> Branch </th>
          <th class="mdl-data-table__cell--non-numeric"> IFSC </th>
          <th class="mdl-data-table__cell--non-numeric"> Account Holder </th>
          <th class="mdl-data-table__cell--non-numeric"> Account NUmber </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="bank in banks" :key="bank.id">
          <td>{{bank.name}}</td>
          <td>{{bank.branch}}</td>
          <td>{{bank.ifsc_code}}</td>
          <td>{{bank.account_holder_name}}</td>
          <td>{{bank.account_number}}</td>
        </tr>
      </tbody>
    </table>
    <dialog-view ref="addFormDialog">
      <div slot="dialog-body">
        <bank-form-view ref="form"></bank-form-view>
      </div>
      <div slot="dialog-actions">
        <button
          class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
          @click="createBank"
        >
        Add
        </button>
      </div>
    </dialog-view>
  </div>
</template>
<script>
import { DialogView } from 'SHARED/dialog'
import { BankFormView } from '.'
import { mapActions, mapGetters } from 'vuex'
import { MESSAGES } from '@/constants'

export default {
  name: 'banks-list-view',
  props: {
    holder: {
      type: Object,
      required: true
    },
    type: {
      type: String,
      required: true
    }
  },
  components: {
    DialogView,
    BankFormView
  },
  watch: {
    holder (value) {
      this.setBankList(value.banks)
    }
  },
  computed: {
    ...mapGetters({
      banks: 'getBankList'
    }),
    name () {
      return this.holder.name || ''
    },
    addForm () {
      return this.$refs.form
    },
    accountParams () {
      return {
        id: this.holder.id,
        type: this.type,
        values: this.addForm.params
      }
    }
  },
  methods: {
    ...mapActions([
      'addBank',
      'setBankList'
    ]),
    toggleDialog (show) {
      this.addForm.reset()
      if (show) {
        this.$refs.addFormDialog.showModal()
      } else {
        this.$refs.addFormDialog.closeModal()
      }
    },
    createBank () {
      this.addForm.valid().then(valid => {
        if (valid) {
          this.addBank(this.accountParams).then(() => {
            this.toggleDialog()
            this.$toastr.s(MESSAGES.success_add)
          }).catch(e => {
            this.$toastr.e(e)
          })
        }
      })
    }
  }
}
</script>



